.sendForm {
    display: flex;
    justify-content: space-between;
    height: 40px;
    background-color: #FFFFFF;
    margin: 73px 0px 32px 0px;
}

.textarea {
    width: 88%;
    min-height: 40px;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    overflow: hidden;
    outline: none;
    resize: none;
    background-color: #F5F7FB;
    line-height: 2.4;
    padding-left: 24px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
}

.button {
    width: 120px;
    height: 40px;
    color: #FFFFFF;
    border-radius: 20px;
    background-color: #0066CC;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
}


